import React from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/swiper.min.css";
import "swiper/components/effect-coverflow/effect-coverflow.min.css";
import "swiper/components/pagination/pagination.min.css";


// import Swiper core and required modules
import SwiperCore, { EffectCoverflow, Pagination } from "swiper/core";

// install Swiper modules
SwiperCore.use([EffectCoverflow, Pagination]);

export default function Queotes() {
  
  return (
    <>
      <Swiper
        effect={"coverflow"}
        grabCursor={true}
        centeredSlides={true}
        slidesPerView={"auto"}
        loop={true}
        coverflowEffect={{
          rotate: 50,
          stretch: 0,
          depth: 100,
          modifier: 1,
          slideShadows: true,
        }}
        pagination={true}
        className="mySwiper"
      >
        <SwiperSlide>
          <img src="/static/gif/1000-7.gif" alt="hmm"/>
        </SwiperSlide>
        <SwiperSlide>
          <img src="/static/gif/academy.gif" />
        </SwiperSlide>
        <SwiperSlide>
          <img src="/static/gif/collosal.gif" />
        </SwiperSlide>
        <SwiperSlide>
          <img src="/static/gif/saitama.gif" />
        </SwiperSlide>
        <SwiperSlide>
          <img src="/static/gif/obito.gif" />
        </SwiperSlide>
        <SwiperSlide>
          <img src="/static/gif/rainer.gif" />
        </SwiperSlide>
      </Swiper>
    </>
  );
}
